"use client";
import { editprofiletriger } from "@/redux/slices/userSlice";
import ToastMessage from "@/Components/ToastMessage";
import axiosInstance from "../interceptor";

export async function BuyerAddAddress(
  type,
  phoneno,
  address,
  country,
  state,
  city,
  area,
  landmark,
  pincode,
  router,
  triger,
  setTriger,
  setLoader,
  AddressDefault,
  firstName,
  companyName
) {
  const data = {
    addressType: type,
    address1: address,
    address2: area,
    city: city,
    landmark: landmark,
    phoneNumber: phoneno,
    postcode: pincode,
    countryId: country,
    zoneId: state,
    isDefault: AddressDefault,
    firstName: firstName,
    lastName: companyName,
  };

  const result = await axiosInstance.post(`customer-address`, data);
  if (result && result.data.status == 1) {
    setTriger(triger + 1);
    ToastMessage({ type: "success", message: result.data.message });
    router.push("/account-settings/my-profile");
    setLoader(false);
  }
}

export async function BuyerEditProfile(
  companyName,
  email,
  firstName,
  phoneno,
  address,
  country,
  state,
  city,
  area,
  landmark,
  pincode,
  newDp,
  router,
  setTriger,
  triger,
  imageupload,
  profilestatus,
  dispatch,
  setLoader
) {
  const data = {
    lastName: companyName,
    emailId: email,
    phoneNumber: phoneno?.toString(),
    address: address,
    address2: area,
    landmark: landmark,
    city: city,
    pincode: pincode,
    stateId: state,
    countryId: country,
    image: imageupload == true ? newDp : undefined,
    firstName: firstName,
  };

  const result = await axiosInstance.post(`customer/edit-profile`, data);
  if (result && result.data.status == 1) {
    router.push("/account-settings/my-profile");
    ToastMessage({ type: "success", message: result.data.message });
    setTriger(triger + 1);
    dispatch(editprofiletriger(!profilestatus));
  }
  setLoader(false);
}

export async function MyProfileEmailVerify(
  email,
  setMinutes,
  setTimer,
  setIsTimerRunning,
  setEmailModel,
  setEmailStatus,
  password
) {
  const data = {
    emailId: email,
    password: password,
  };

  const result = await axiosInstance.put(`customer/change/mail`, data);
  if (result && result.data.status == 1) {
    ToastMessage({ type: "success", message: result.data.message });
    setMinutes(1);
    setTimer(59);
    setIsTimerRunning(true);
    setEmailModel(true);
    setEmailStatus(true);
  }
}

export async function MyProfileEmailOtpverify(
  email,
  otp,
  setMinutes,
  setTimer,
  setIsTimerRunning,
  setEmailModel,
  setOtpStatus
) {
  const data = {
    emailId: email,
    otp: Number(otp),
  };

  const result = await axiosInstance.put(`customer/mail/verify`, data);
  if (result && result.data.status == 1) {
    ToastMessage({ type: "success", message: result.data.message });
    localStorage.setItem("email", result.data?.data?.email);
    setMinutes(0);
    setTimer(0);
    setIsTimerRunning(false);
    setEmailModel(false);
    setOtpStatus(true);
  }
}

export async function ResetPasswordApi(newPassword, key, router, setLoader) {
  const data = {
    newPassword: newPassword,
    key: key,
  };

  const result = await axiosInstance.put(`customer/reset-password`, data);
  if (result && result?.data?.status == 1) {
    ToastMessage({ type: "success", message: result.data.message });
    router.push("/buyer-login");
  }
  setLoader(false);
}

export async function MyprofileAddressEdit(
  id,
  setAddressEdit,
  triger,
  setTriger
) {
  const result = await axiosInstance.get(`customer-address/${id}`);
  if (result && result?.data?.status == 1) {
    setAddressEdit(result?.data?.data);
    setTriger(triger + 1);
  }
}

