export async function MyprofileAddressDelete(
  id,
  triger,
  setTriger,
  handleAddress
) {
  const result = await axiosInstance.delete(`customer-address/${id}`);
  if (result && result?.data?.status == 1) {
    ToastMessage({ type: "success", message: result.data.message });
    setTriger(triger + 1);
    handleAddress();
  }
}

export async function DefaultAddressApi(id, type, handleAddress) {
  const data = {
    addressType: type,
  };

  const result = await axiosInstance.put(
    `customer-address/update-default/${id}`,
    data
  );
  if (result && result?.data?.status == 1) {
    handleAddress();
    ToastMessage({ type: "success", message: result.data.message });
  }
}
